import React from "react";
import { withStateHandlers, compose } from "recompose";

import { Container, FormTitle, FormSubtitle, MarketoForm } from "./elements";

const _SignUpForm = ({ success, setSuccess }) => (
  <Container>
    <FormTitle>Get in touch</FormTitle>
    {!success && (
      <FormSubtitle>
        {"Questions or suggestions? We're all ears."}
        <br />
        Use the secure form below to drop us a line.
      </FormSubtitle>
    )}
    <MarketoForm
      munchkinId="166-VEV-188"
      formId={1051}
      successCallback={setSuccess}
    />
  </Container>
);

const _stateHandlers = [
  {
    success: false,
  },
  {
    setSuccess: () => () => ({ success: true }),
  },
];

export default compose(withStateHandlers(..._stateHandlers))(_SignUpForm);
